import React, { useState } from 'react'
import data from './data'
import List from './List'
function App() {
  const [people, setPeople] = useState(data)
  const [age, setAge] = useState('')
  const [picture, setPicture] = useState('')
  const [name, setName] = useState('')
  const submitFunction = (e) => {
    e.preventDefault()
    if (name !== '' || age !== '' || picture !== '' || isNaN(age)) {
      const person = {
        id: new Date().getTime().toString(),
        name,
        age,
        image: picture,
      }
      setPeople((people) => {
        return [...people, person]
      })
      setName('')
      setAge('')
      setPicture('')
      console.log(people)
    } else {
      alert('Please fill all the fields right')
    }
  }
  function numberFinder(e) {
    let val = e.target.value
    if (isNaN(val)) {
      val = ''
      alert('Please put a number here')
    } else {
      setAge(val)
    }
  }
  return (
    <>
      <main>
        <div className="container">
          <h3>{people.length} Birtdays Today</h3>
          <List people={people} />
          <button type="button" onClick={() => setPeople([])}>
            Erase Birtdays
          </button>
        </div>
      </main>
      <div className="form">
        <form onSubmit={submitFunction}>
          <h2 className="title">
            Today is the Birtday of {name} of {age} Years Old
          </h2>
          <div className="center">
            <img src={picture} alt="Birtday Pic" />
          </div>

          <p>Enter the name of the Birtday Person:</p>
          <input
            type="text"
            name="username"
            onChange={(e) => setName(e.target.value)}
            id="name"
          />
          <p>Enter his/her age:</p>
          <input type="text" name="age" onChange={numberFinder} id="age" />
          <p>Click on the chosen button to send the birtday pic</p>
          <input
            type="file"
            id="myFile"
            name="filename"
            onChange={(e) => setPicture(URL.createObjectURL(e.target.files[0]))}
          ></input>
          <button
            type="button"
            onClick={() => document.getElementById('myFile').click()}
            className="send"
          >
            Send the Birtday Picture
          </button>
          <button type="submit" value="Submit" className="send">
            Add a New Birthday Person
          </button>
        </form>
      </div>
    </>
  )
}

export default App
